import React, {useState, useEffect} from 'react';
import {connect} from 'react-redux';
import HeroComponents from "components/layout-components/CustomrPageHeaderAlt";
import ContainerTailwind from "components/layout-components/ContainerTailwind";
import {Button, Card, Divider, Drawer, Form, Input, message, Modal, Select, Table} from "antd";
import {
	DeleteOutlined,
	EditOutlined, PlusOutlined,
	ReloadOutlined, SettingOutlined
} from "@ant-design/icons";
import {Link, useHistory} from 'react-router-dom'
import ApiService from "services/api.service";

function mapStateToProps(state) {
	return {};
}

function Index() {
	
	const defaultColumns = [
		{
			visible:true,
			key:"no",
			title:"No",
			dataIndex: "id",
			width: 70,
			render: (_, val, index) => ((index + 1) + (params?.limit * (params?.page - 1)))
		},
		{
			visible:true,
			key:"display_name",
			dataIndex: ['data', 'name'],
			title: "Display Name",
			render: (_) => _ ?? "-"
		},
		{
			visible:true,
			key:"code",
			dataIndex: ['data', 'code'],
			title: "Code",
			render: (_) => _ ?? "-"
		},
		{
			visible:true,
			key: 'action',
			width: 120,
			dataIndex: 'id',
			render: (_, val, index) => {
				return (
					<div className={'text-right d-flex !mk-gap-2'}>
						<Button size={'small'} type={'primary'} ghost icon={<EditOutlined/>}
						        onClick={() => handleClickEdit(val, index)}/>
						<Button
							size={'small'}
							type={'primary'}
							danger
							ghost
							onClick={() => {
								handleDeleteRow(_, index)
							}}
							loading={loadings[index] ?? false}
							icon={<DeleteOutlined/>}/>
					</div>
				)
			}
		},
	]
	const history = useHistory()
	const [loading, setLoading] = useState(true)
	const [params, setParams] = useState({
		page: 1,
		limit: 20
	})
	const [pagination, setPagination] = useState({
		page: 1,
		limit: 10
	})
	const [data, setData] = useState([])
	
	const [loadings, setLoadings] = useState([])
	
	const [visibleDrawer, setVisibleDrawer] = useState(false)
	const [visible, setVisible] = useState(false)
	const [selected, setSelected] = useState(null)
	const [form] = Form.useForm();
	
	function _caller() {
		setLoading(true)
		new ApiService({
			url: "/api/v1/master-data/airline-type",
			mode:"express"
		})
			.get()
			.then((response) => {
				setData([...response?.data])
				setLoading(false)
			})
			.catch((err) => {
				setData([])
				setLoading(false)
			})
	}
	
	
	useEffect(() => {
		_caller()
	}, [params])
	
	
	const enterLoading = (index, value = true) => {
		setLoadings((prevLoadings) => {
			prevLoadings[index] = value;
			return prevLoadings ?? []
		});
	};
	
	const onFinish = () => {
		form.validateFields()
			.then((value) => {
				new ApiService({
					body: value,
					url: `/api/v1/master-data/airline-type`,
					mode:"express"
				}).post()
					.then((response) => {
						message.success({
							content: response?.message ?? "Successfully created",
							key: "create-city",
							duration: 2
						})
						setLoading(false)
					})
					.catch((err) => {
						message.error({
							content: err?.message ?? "Error: Some Error",
							key: "create-city",
							duration: 2
						})
						setLoading(false)
					})
			})
			.catch((err) => {
			
			})
	}
	
	useEffect(() => {
		form.resetFields()
	}, [visible])
	
	function handleClickEdit(_, index) {
		setVisible(!visible)
		setSelected(_)
		
	}
	
	function onCloseModal() {
		setVisible(false)
		setSelected(null)
	}
	
	
	function handleDeleteRow(_, index) {
		enterLoading(index, true)
		new ApiService({
			url: `/api/v1/master-data/airline-type/${_}`,
		}).delete()
			.then((response) => {
				if (response?.success) {
					if (data && typeof (data[index]) !== 'undefined') {
						data[index].isDeleted = true
						setData([...data])
						enterLoading(index, false)
					}
					message.success(response?.message ?? "Successfully deleted")
					setTimeout(() => {
						setData([...data.filter((item) => item.id !== _)])
					}, 2000)
				} else {
					enterLoading(index, false)
					message.error(response?.message ?? "Error: delete by id " + _)
				}
			})
			.catch((err) => {
				enterLoading(index, false)
			})
	}
	
	const [columns, setColumns] = useState(defaultColumns ?? [])
	
	const onChangeDrawer = (val) => {
		setVisibleDrawer(val ?? false)
	}
	const onColumnsChange = (value) => {
		setColumns(() => {
			return defaultColumns.filter((item) => value.includes(item.key))
		})
	}
	
	function Extra(){
		return (
			<div className={'d-flex align-items-center mk-gap-2'}>
				<Button icon={<ReloadOutlined/>} ghost type={'primary'} onClick={_caller} loading={loading}/>
			</div>
		)
	}
	
	return (
		<React.Fragment>
			<Drawer
				title={`Filter`}
				placement={"right"}
				closable
				onClose={() => onChangeDrawer(false)}
				visible={visibleDrawer}
			>
				<div className={'w-100'}>
					<h5>Table Column</h5>
					<Select onChange={onColumnsChange} mode={'tags'} className={'w-100'} placeholder={'Select Columns Active'}
					        defaultValue={defaultColumns.map((item) => item?.key) ?? []}>
						{
							defaultColumns.map((item) => (
								<Select.Option value={item.key} key={item.key}>{`${item.key}`.replace(/_/g, ' ')}</Select.Option>
							))
						}
					</Select>
					<Divider/>
				</div>
			</Drawer>
			
			<Modal
				style={{
					width: "520px !important",
				}}
				title={'Edit'}
				visible={visible} onOk={onFinish} onCancel={onCloseModal}>
				<Form form={form} initialValues={{
					...selected,
				}} layout={'vertical'}>
					<Form.Item name={'identifier'} label={'Identifier'} hidden>
						<Input/>
					</Form.Item>
					<Form.Item name={['data', 'code']} label={'Code'} rules={[{required: true, message: "Can't be empty!"}]}>
						<Input/>
					</Form.Item>
					<Form.Item name={['data', 'name']} label={'Display Name'}
					           rules={[{required: true, message: "Can't be empty!"}]}>
						<Input/>
					</Form.Item>
					
					<Form.Item
						name={['data', 'type']} label={'Data Overview on Menu'}
						hasFeedback
					>
						<Select allowClear showArrow showSearch placeholder={'leave blank if not for arrival or departure'}>
							{[
								{label:"Gate",value:"gate"},
								{label:"Counter",value:"counter"},
								{label:"Baggage-belt",value:"baggage-belt"}
							].map((item,index)=> (
								<Select.Option value={item?.value} key={item?.value}>{item?.label}</Select.Option>
							))
							}
						</Select>
					</Form.Item>
				</Form>
			</Modal>
			
			<HeroComponents
				loading={false}
				name={"Overview"}
				positionName={['Display','Type']}
				thumbnail={'/overview.png'}
				extra={<div className={'text-right mk-gap-4 d-flex'}>
					<Button type={'primary'} size={'small'} ghost icon={<PlusOutlined/>} onClick={() => {
						history.push('/dashboard/display/type/create')
					}}>Add New</Button>
				</div>}
			/>
			
			<div style={{marginTop: 90}}>
				<ContainerTailwind>
					<Card bordered={false}
					      extra={<Extra/>}>
						<Table
							loading={loading}
							rowClassName={(record, index) => record?.isDeleted ? "!mk-bg-red-500 !mk-bg-opacity-20" : ''}
							dataSource={data}
							columns={columns.filter((item)=> item?.visible === true)}
							pagination={{
								pageSize: params?.limit,
								current: params?.page,
								total: pagination?.total_items ?? 0,
								onChange: function (page, pageSize) {
									setParams({
										...params,
										page,
										limit: pageSize
									})
								}
							}}
						/>
					</Card>
				</ContainerTailwind>
			</div>
		</React.Fragment>
	);
}

export default connect(
	mapStateToProps,
)(Index);